const actions = {
  getUnits: (page, search, filter) => ({
    type: 'GET_UNITS_REQUEST',
    payload: {
      page: page,
      search: search,
      filter: filter
    }
  }),
  createUnit: () => ({
    type: 'CREATE_UNIT_REQUEST'
  }),
  storeUnit: data => ({
    type: 'STORE_UNIT_REQUEST',
    payload: { body: data }
  }),
  editUnit: id_unit => ({
    type: 'EDIT_UNIT_REQUEST',
    payload: { id_unit: id_unit }
  }),
  updateUnit: (id_unit, data) => ({
    type: 'UPDATE_UNIT_REQUEST',
    payload: {
      id_unit: id_unit,
      body: data
    }
  }),
  showUnit: id_unit => ({
    type: 'SHOW_UNIT_REQUEST',
    payload: { id_unit: id_unit }
  }),
  deactivateUnit: id_unit => ({
    type: 'DEACTIVATE_UNIT_REQUEST',
    payload: { id_unit }
  }),
  activateUnit: id_unit => ({
    type: 'ACTIVATE_UNIT_REQUEST',
    payload: { id_unit }
  }),
  clearSuccess: () => ({
    type: "CLEAR_SUCCESS"
  }),
  clearError: () => ({
    type: "CLEAR_ERROR"
  }),
  clearErrorField: () => ({
    type: "CLEAR_ERROR_FIELD"
  })
};

export default actions;
