import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";
import { Link } from "react-router-dom";

import notification from "../../../../components/notification";
import { Empty, Input, Tooltip } from "antd";

import Popconfirms from "../../../../components/feedback/popconfirm";
// import Box from '../../components/utility/box';
// import HelperText from "../../../../components/utility/helper-text";
import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Scrollbars from "../../../../components/utility/customScrollBar";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/item/unit/actions";
import TableWrapper from "../../../Tables/antTables/antTable.style";
import CardWrapper, { Box, StatusTag } from "../../../App/app.style";
import getLanguage from "../../../App/MyLanguange";

// const Panel = Collapse.Panel;
// const Option = Select.Option;
const Search = Input.Search;
const redirectTo = "/dashboard/setup/menus";

class Unit extends Component {
  state = {
    filter: {
      //id_cash_flow_group: [],
      transaction_type: [],
      pagination: null
    },
    search: null,
    transaction_types: [
      {
        id: 1,
        name: "All"
      },
      {
        id: 2,
        name: "Purchase"
      },
      {
        id: 3,
        name: "Sales"
      }
    ]
  };

  // Get All data diawal
  componentDidMount() {
    const { getUnits } = this.props;
    getUnits();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.unit.error) {
      notification("error", this.props.unit.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.unit.success) {
      notification("success", this.props.unit.success);
      this.props.clearSuccess();
    }
  }

  // Handle search
  handleSearch = value => {
    // console.log(value);
    const { getUnits } = this.props;
    this.setState({
      filter: {
        //id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    getUnits(1, value);
  };

  //handle Search Value
  handleSearchValue = e => {
    const value = e.target.value;

    this.setState({ search: value });
  };

  // Handle Reset search
  handleResetSearch = () => {
    const { getUnits } = this.props;
    this.setState({
      filter: {
        id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getUnits();
  };

  // Handle filter change
  handleFilterChange = (name, value) => {
    this.setState({
      filter: {
        ...this.state.filter,
        [name]: value
      }
    });
  };

  // Handle filter
  handleFilter = () => {
    const { getUnits } = this.props;
    //const { current_page } = this.props.unit;
    const { filter } = this.state;
    let string = "";

    if (filter.transaction_type) {
      string += "transaction_type=" + filter.transaction_type;
    }
    if (filter.pagination) {
      string
        ? (string += "&pagination=" + filter.pagination)
        : (string += "pagination=" + filter.pagination);
    }

    this.setState({ search: null });
    getUnits(1, null, string);
  };

  // Handle Reset Filter
  handleResetFilter = () => {
    const { getUnits } = this.props;
    this.setState({
      filter: {
        //id_cash_flow_group: [],
        transaction_type: [],
        pagination: null
      }
    });
    this.setState({ search: null });
    getUnits();
  };

  // Handle action di table
  handleRecord = (action, id) => {
    if (action === "activate") {
      this.props.activateUnit(id);
    } else {
      this.props.deactivateUnit(id);
    }
  };

  // Handle pagination
  handlePageChange = page => {
    const { getUnits } = this.props;
    const { search } = this.props.unit;
    const { filter } = this.props.unit;
    getUnits(page, search, filter);
  };

  listAction = {
    title: "",
    dataIndex: "id_unit",
    rowKey: "id_unit",
    width: "10%",
    align: "center",
    render: (text, row) => (
      <div>
        <Tooltip
          placement="topLeft"
          title={<IntlMessages id="tooltip.update" />}
        >
          <Link to={`${this.props.match.path}/update/${text}`}>
            <Button size="small" className="invoiceEdtBtn">
              <i className="ion-android-create" />
            </Button>
          </Link>
        </Tooltip>

        {row.is_active ? (
          <Popconfirms
            title={<IntlMessages id="units.popconfirm.deactivate" />}
            onConfirm={this.handleRecord.bind(this, "deactivate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.deactivate" />}
            >
              <Button size="small" className="invoiceDltBtn">
                <i className="ion-close-circled" />
              </Button>
            </Tooltip>
          </Popconfirms>
        ) : (
          <Popconfirms
            title={<IntlMessages id="units.popconfirm.activate" />}
            onConfirm={this.handleRecord.bind(this, "activate", text)}
            okText={getLanguage({ id: "yes" })}
            cancelText={getLanguage({ id: "no" })}
          >
            <Tooltip
              placement="topRight"
              title={<IntlMessages id="tooltip.activate" />}
            >
              <Button size="small" className="invoiceRstBtn">
                <i className="ion-refresh" />
              </Button>
            </Tooltip>
          </Popconfirms>
        )}
      </div>
    )
  };

  render() {
    // console.log(this.props.unit);
    const columns = [
      {
        title: getLanguage({ id: "code" }),
        dataIndex: "unit_code",
        rowKey: "unit_code",
        width: "35%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "name" }),
        dataIndex: "unit_name",
        rowKey: "unit_name",
        width: "50%",
        render: text => <span>{text}</span>
      },
      {
        title: getLanguage({ id: "isDefault" }),
        dataIndex: "is_default",
        rowKey: "is_default",
        width: "5%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "primary";
            display = <IntlMessages id="yes" />;
          } else {
            className = "nonactive";
            display = <IntlMessages id="no" />;
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      },
      {
        title: getLanguage({ id: "status" }),
        dataIndex: "is_active",
        rowKey: "is_active",
        width: "10%",
        align: "center",
        render: text => {
          let className;
          let display;
          if (text === 1) {
            className = "active";
            display = getLanguage({ id: "active" });
          } else {
            className = "nonactive";
            display = getLanguage({ id: "inactive" });
          }
          return <StatusTag className={className}>{display}</StatusTag>;
        }
      }
    ];
    // Data setup
    const { units } = this.props.unit;
    // console.log(units); //console.log(this.state);
    const dataSource = [];
    const { user } = this.props.auth;
    let { permissions } = user ? user : [];
    permissions = permissions ? permissions : [];

    //console.log(permissions);
    if (permissions.includes("Update Unit") && columns.length === 4) {
      columns.push(this.listAction);
    }

    Object.keys(units).map((unit, index) => {
      return dataSource.push({
        ...units[unit],
        key: unit
      });
    });

    // Pagination Setup
    const { current_page, page_size, total } = this.props.unit;

    //console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="units.pageHeader" />}
            marginBottom={true}
          >
            <div style={{ display: "flex", justifyContent: "flex-end" }}>
              {permissions.includes("Create Unit") ? (
                <Link to={`${this.props.match.path}/add`}>
                  <Button type="primary" className="mateAddInvoiceBtn">
                    <IntlMessages id="units.button.add" />
                  </Button>
                </Link>
              ) : null}
              <Button
                onClick={() => this.props.history.replace(redirectTo)}
                style={{ marginLeft: "5px" }}
              >
                <IntlMessages id="button.back" />
              </Button>
            </div>
          </PageHeader>
          <div className="isoInvoiceTableBtn">
            <Search
              placeholder={
                getLanguage({ id: "code" }) + ", " + getLanguage({ id: "name" })
              }
              onSearch={this.handleSearch}
              style={{ marginRight: "1%" }}
              value={this.state.search}
              onChange={this.handleSearchValue}
            />
            <div
              style={{
                // width: "20%",
                marginTop: "auto",
                marginRight: "1%"
              }}
            >
              <Button
                type="primary"
                onClick={this.handleSearch.bind(this, this.state.search)}
              >
                <IntlMessages id="button.search" />
              </Button>
            </div>

            <div
              style={{
                width: "30%",
                marginTop: "auto",
                marginRight: "27%"
                // marginLeft: "-2%"
              }}
            >
              <Button onClick={this.handleResetSearch}>
                <IntlMessages id="filter.reset" />
              </Button>
            </div>
          </div>

          <CardWrapper>
            {units.length === 0 && !this.props.unit.loading ? (
              <Empty description={<IntlMessages id="units.table.noData" />} />
            ) : (
              <div className="isoInvoiceTable">
                <Scrollbars style={{ width: "100%" }}>
                  <TableWrapper
                    dataSource={dataSource}
                    columns={columns}
                    loading={this.props.unit.loading}
                    className="invoiceListTable"
                    bordered={true}
                    // Set pagination, jika ada page_size mengunakan pagination API
                    // jika tidak ada page_size menggunakan pagination bawaan
                    pagination={
                      page_size
                        ? {
                            current: current_page,
                            pageSize: parseInt(page_size),
                            total: total,
                            onChange: this.handlePageChange.bind(this)
                          }
                        : true
                    }
                  />
                </Scrollbars>
              </div>
            )}
          </CardWrapper>
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    unit: state.unit,
    auth: state.auth
  };
};

export default compose(connect(mapStateToProps, actions))(Unit);
